import Student from '../../model/Student';
import {StudentState} from '@/store/student/types';
import {ActionTree, GetterTree, Module, MutationTree} from 'vuex';
import {RootState} from '@/store/types';

const state: StudentState = {
    student: new Student(),
};

const getters: GetterTree<StudentState, RootState> = {
    GET_STUDENT: (state) => {
        return state.student;
    },
};

const mutations: MutationTree<StudentState> = {
    SET_STUDENT: (state, student: Student) => {
        state.student = student;
    },
};

const actions: ActionTree<StudentState, RootState> = {
    'action/SET_STUDENT': (context, student: Student) => {
        context.commit('SET_STUDENT');
        context.rootState.activeStudent = student;
    },
};

export const student: Module<StudentState, RootState> = {
    state,
    getters,
    mutations,
    actions,
};
