import axios from 'axios';
import Student from '../../model/Student';
import Lecturer from '../../model/Lecturer';
import {RegisterState} from '@/store/register/types';
import {ActionTree, GetterTree, Module, MutationTree} from 'vuex';
import {RootState} from '@/store/types';

const state: RegisterState = {
    successful: false,
    error: '',
};

const getters: GetterTree<RegisterState, RootState> = {
    REGISTERED: (state) => {
        return state.successful;
    },
    REGISTER_ERROR: (state) => {
        return state.error;
    },
};

const mutations: MutationTree<RegisterState> = {
    ADD_STUDENT: (state, student: Student) => {
        axios.post('/auth/register/student', student.json)
            .then(() => {
                state.successful = true;
            })
            .catch((reason) => state.error = reason.toString());
    },
    ADD_LECTURER: (state, lecturer: Lecturer) => {
        axios.post('/auth/register/student', lecturer.json)
            .then(() => {
                state.successful = true;
            })
            .catch((reason) => state.error = reason.toString());
    },
};

const actions: ActionTree<RegisterState, RootState> = {
    REGISTER_STUDENT: (context, student: Student) => {
        context.commit('ADD_STUDENT', student);
    },
    REGISTER_LECTURER: (context, lecturer: Lecturer) => {
        context.commit('ADD_LECTURER', lecturer);
    },
};

export const register: Module<RegisterState, RootState> = {
    state,
    getters,
    mutations,
    actions,
};
